//GENERAL STYLES

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Arial;
  position: relative;
}

.container {
  width: 1595px;
  display: flex;
  margin: 0 auto;
  padding: 0 15px;
  overflow: hidden;
}

a {
  display: inline-block;
  text-decoration: none;
}

button {
  cursor: pointer;
  background: none;
  border: none;
}

li {
  list-style-type: none;
}

.line {
  background-color: #d8d8d8;
  border: 1px solid #d8d8d8;
  height: 980px;
}

.text16 {
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 21px;
}

.text18 {
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 24px;
}

.text24 {
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 32px;
}

.blue {
  color: #1DA1F2;
}



//LOGIN PAGE

.login {
  &_box {
    display: flex;
    flex-direction: column;
    width: 450px;
    margin: 0 auto;
    margin-top: 60px;
  }

  &_title {
    font-style: normal;
    font-weight: 900;
    font-size: 42px;
    line-height: 49px;

    color: #000000;
    margin: 36px 0;
  }

  &_form {
    display: flex;
    flex-direction: column;
  }

  &_input {
    background: #FFFFFF;
    border: 1px solid rgba(0, 0, 0, 0.2);
    box-sizing: border-box;
    border-radius: 6px;
    width: 450px;
    padding: 23px 0 23px 20px;
    margin-bottom: 25px;
    font-size: 18px;

    &::placeholder {
      font-style: normal;
      font-weight: normal;
      font-size: 18px;
      line-height: 24px;
      color: rgba(0, 0, 0, 0.6);
    }
  }

  &_btn {
    font-style: normal;
    font-weight: bold;
    font-size: 18px;
    line-height: 24px;
    text-align: center;
    color: #FFFFFF;
    background: #1DA1F2;
    border-radius: 76px;
    width: 450px;
    padding: 18px 0;
  }
}



