//PROFILE STYLE

.profile {
  &_header {
    padding: 22px 0 15px 31px;
    display: flex;
    align-items: center;

    &_name {
      margin-left: 40px;
    }
  }
  &_section{
    margin-bottom: 50px;
    display: flex;
  }

  &_box {
    padding-left: 25px;
    padding-right: 24px;

    &-top {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 10px;
    }
  }

  &_edit-btn {
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 24px;
    color: #000000;
    border: 1px solid rgba(0, 0, 0, 0.4);
    box-sizing: border-box;
    border-radius: 50px;
    width: 120px;
    padding: 10px 0;
  }

  &_list {
    display: flex;
    margin-top: 15px;

    &-text {
      margin-left: 5px;
      color: #000000;
      opacity: 0.6;
    }
  }

  &_item {
    display: flex;
  }

  &_item:not(:last-child) {
    margin-right: 31px;
  }

  &_menu {
    display: flex;

    &-item:not(:last-child) {
      margin-right: 177px;
    }

    &-item {
      padding-bottom: 15px;
      border-bottom: 4px solid transparent;
    }

    &-item:hover {
      font-style: normal;
      font-weight: bold;
      font-size: 18px;
      line-height: 24px;
      color: #000000;


      border-bottom: 4px solid #1DA1F2;
    }

    &-link {
      color: #000000;
    }
  }

  &_first-box {
    padding-top: 15px;
    padding-left: 25px;
    padding-bottom: 20px;
    margin-right: 25px;
  }

  &_second-box {
    padding-top: 10px;
    padding-left: 25px;
    padding-bottom: 20px;
    margin-right: 25px;
  }

  &_third-box {
    padding-top: 10px;
    padding-left: 25px;
    padding-bottom: 20px;
    margin-right: 25px;
  }
  &_left_box{
    padding-top: 20px;
    padding-left: 21px;
  }
  &_img-box{
    margin-bottom: 20px;
  }
}

