.left_section {
   display: flex;
}
.left-box{
    margin-top: 31px;
}
.nav {
    display: flex;
    flex-direction: column;

    &_links {
        display: flex;
        align-items: center;
        font-style: normal;
        font-weight: 600;
        font-size: 18px;
        line-height: 24px;
        color: #000000;
        text-decoration: none;
        opacity: 0.7;
    }
}

.tweet_btn {
    font-style: normal;
    font-weight: bold;
    font-size: 18px;
    line-height: 24px;
    color: #FFFFFF;
    background: #1D9BF0;
    border-radius: 27.5px;
    width: 230px;
    padding: 16px 0;
    border: none;
}

.active {
    opacity: 1;
}

.home {
    margin-bottom: 30px;

    &::before {
        content: "";
        width: 28px;
        height: 28px;
        display: inline-block;

        background-image: url('../../Images/home.svg');
        background-repeat: no-repeat;
        background-size: 28px 28px;
        margin-right: 20px;
    }
}

